import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'drag-points',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M320 192h128v128H320zm256 0h128v128H576zM320 448h128v128H320zm256 0h128v128H576zM320 704h128v128H320zm256 0h128v128H576z" />
</span>
}

const DragPoints = props => <SvgIcon _type="drag-points" {...props}>
  {svgData.data.props.children}
</SvgIcon>

DragPoints.svgData = svgData

export default DragPoints


