import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'success-filled',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M512 64a448 448 0 100 896 448 448 0 000-896zm-38.464 615.296l-42.368-43.712-146.112-159.552h85.952l99.776 104.128 182.144-232.128h86.016L473.536 679.296z" />
</span>
}

const SuccessFilled = props => <SvgIcon _type="success-filled" {...props}>
  {svgData.data.props.children}
</SvgIcon>

SuccessFilled.svgData = svgData

export default SuccessFilled


