import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'line',
  data: <span>
  <path d="M0 731.282h1024v73.143H0v-73.143z" />
</span>
}

const Line = props => <SvgIcon _type="line" {...props}>
  {svgData.data.props.children}
</SvgIcon>

Line.svgData = svgData

export default Line


