import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'circle',
  data: <span>
  <path d="M64.403 512.046a448.177 448.177 0 10896.354 0 448.177 448.177 0 10-896.354 0z" />
</span>
}

const Circle = props => <SvgIcon _type="circle" {...props}>
  {svgData.data.props.children}
</SvgIcon>

Circle.svgData = svgData

export default Circle


