import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'rectangle',
  data: <span>
  <path d="M0 146.286v731.428h1024V146.286H0zM950.857 804.57H73.143V219.43h877.714V804.57z" />
</span>
}

const Rectangle = props => <SvgIcon _type="rectangle" {...props}>
  {svgData.data.props.children}
</SvgIcon>

Rectangle.svgData = svgData

export default Rectangle


