import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'data-directory',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M195.072 366.08h64v384h-64zm192.256-256h64v640h-64zm190.4 384h64v256h-64zm192.32-256h64v512h-64zM66.56 811.52h896v64h-896z" />
</span>
}

const DataDirectory = props => <SvgIcon _type="data-directory" {...props}>
  {svgData.data.props.children}
</SvgIcon>

DataDirectory.svgData = svgData

export default DataDirectory


