/**
 * Svg Icon
 *
 * @author zhangpc
 * @date 2018-10-25
 */
import React from 'react';
import PropTypes, {string} from 'prop-types';
import classNames from 'classnames'

function Icon(props) {
  const {
    _type, children, style = {}, size, color, spin, className,
    component: Component, ...other
  } = props;

  const svgProps = {};
  if (Component === 'svg') {
    svgProps.viewBox = '0 0 1024 1024';
    svgProps.preserveAspectRatio = 'xMidYMid slice';
  }
  const _style = JSON.parse(JSON.stringify(style))
  if (size) {
    _style.fontSize = size
  }
  if (color) {
    _style.fill = color
  }

  const classString = classNames('tenx-icon', {
    [`tenx-icon-${_type}`]: !!_type,
    'tenx-icon-spin': !!spin,
  }, className)

  return (
    <i className={classString}>
      <Component
        className="tenx-icon-svg"
        style={_style}
        aria-hidden="true"
        {...svgProps}
        {...other}
      >
        {children}
      </Component>
    </i>
  );
}

Icon.propTypes = {
  /**
   * The type of the icon for compatible v1.x style.
   */
  _type: PropTypes.string,
  /**
   * The name of the icon font ligature.
   */
  children: PropTypes.node,
  /**
   * @ignore
   */
  style: PropTypes.object,
  /**
   * @ignore
   */
  className: PropTypes.string,
  /**
   * 
   */
  color: PropTypes.string,
  /**
   * /
   */
  size: PropTypes.number,
  /**
   * 
   */
  spin: PropTypes.bool,
  /**
   * The component used for the root node.
   * Either a string to use a DOM element or a component.
   */
  component: PropTypes.oneOfType([PropTypes.string, PropTypes.func, PropTypes.object]),
};

Icon.defaultProps = {
  component: 'svg',
};

export default Icon;

export const CustomIcon = props => {
  const { data, ...res } = props;
  if(!data) {
    console.warn(
      'CustomIcon\'s data is null'
    )
    return null
  }
  const _data = JSON.parse(data);
  return <Icon _type={data.name} dangerouslySetInnerHTML={{ __html: _data.data }} {...res} />;
};
CustomIcon.PropTypes = {
  data: PropTypes.string.isRequired,
  ...Icon.PropTypes,
}



