import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'atom-index',
  data: <span>
  <defs>
    <style>
      {`@font-face{font-family:element-icons;src:url(chrome-extension://moombeodfomdpjnpocobemoiaemednkg/fonts/element-icons.woff) format("woff"),url("chrome-extension://moombeodfomdpjnpocobemoiaemednkg/fonts/element-icons.ttf ") format("truetype")}`}
    </style>
  </defs>
  <path d="M512 123.712L64 900.224h896L512 123.712zm33.088 492.416V313.536l265.088 459.456-265.088-156.864zm-66.176-302.592v300.8L214.208 772.352l264.704-458.816zm31.616 358.976l272.896 161.6H239.936l270.592-161.6z" />
</span>
}

const AtomIndex = props => <SvgIcon _type="atom-index" {...props}>
  {svgData.data.props.children}
</SvgIcon>

AtomIndex.svgData = svgData

export default AtomIndex


