import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'star',
  data: <span>
  <path d="M512 25.6l166.4 320 345.6 51.2-268.8 268.8L832 998.4 512 819.2 192 998.4l76.8-332.8L0 396.8l345.6-51.2L512 25.6z" />
</span>
}

const Star = props => <SvgIcon _type="star" {...props}>
  {svgData.data.props.children}
</SvgIcon>

Star.svgData = svgData

export default Star


