import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'editor-setting',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M64 64h896v64H64zm0 384h896v64H64zm0 448h896v64H64z" />
</span>
}

const EditorSetting = props => <SvgIcon _type="editor-setting" {...props}>
  {svgData.data.props.children}
</SvgIcon>

EditorSetting.svgData = svgData

export default EditorSetting


