import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'split-rule',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M480 64h64v896h-64zM320 256v512H128V256h192m64-64H64v640h320V192zm512 64v512H704V256h192m64-64H640v640h320V192z" />
</span>
}

const SplitRule = props => <SvgIcon _type="split-rule" {...props}>
  {svgData.data.props.children}
</SvgIcon>

SplitRule.svgData = svgData

export default SplitRule


