import React from 'react'
import SvgIcon from './lib/SvgIcon'

export const svgData = {
  name: 'custom-resource',
  data: <span>
  <defs>
    <style />
  </defs>
  <path d="M512 8v448h512V8H512zm448 384H576V72h384v320z" />
  <path d="M832 516.992v383.04H64v-640h388.416v-64H0v768h896v-447.04z" />
</span>
}

const CustomResource = props => <SvgIcon _type="custom-resource" {...props}>
  {svgData.data.props.children}
</SvgIcon>

CustomResource.svgData = svgData

export default CustomResource


